button.menu-trigger {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 8px;

	img.avatar {
		height: 28px;
		width: 28px;
		border-radius: 50%;
		flex-shrink: 0;
		object-fit: cover;
	}
}

div.menu {
	width: 250px;
	padding: 8px 0;

	a {
		height: 40px;

		mat-icon {
			margin-right: 8px !important;
		}
	}
}

.agenthub-footer {
	position: fixed;
	bottom: 0;
	width: 100%;
	z-index: var(--z-index-bottom-navbar);
}

:host.home-banner-nav-items {
	a.head-link {
		color: var(--basic-1) !important;
		padding: 1rem 0;
	}
}

a.item-link {
	width: 280px !important;
	min-height: 45px;
	color: var(--general-5);

	&:not(:last-child) {
		border-bottom: 1px solid var(--neutral-1);
	}
}

a.head-link-active {
	border-bottom: 3px solid var(--prim-2);
	border-radius: 0;
	color: var(--prim-2) !important;

	transition: 0.2s ease-in-out;
}
.mob-header {
	font-size: 26px;
	margin-right: 13px;
}
.header-panel {
	margin-bottom: -40px !important;
	top: 8px;
	position: relative;
}
.menu-header {
	background: none;
	border: none;
	left: 10px;
}
.header-redirect {
	z-index: 1000;
}
.icn-menu {
	background: var(--basic-1) !important;
	color: var(--basic-2) !important;
}
